<template>
  <div class="message-container">

    <div style="display: flex; flex-direction: column;  justify-content: center;height: 100%; margin-left:2rem ;">
      <div :class="['phone', isLight ? 'light' : 'dark']">
        <div id="top-menu">
          <div class="button">
            <i class="fas fa-arrow-left"></i>
          </div>
          <button id="mode" class="button" @click="isLight = !isLight">
            <i :class="['bi bi-brightness-high-fill', { 'bi-moon': !isLight }]"></i>

          </button>
          <div class="button">
            <i class="fas fa-bars"></i>
          </div>
        </div>

        <div class="thumbnail-border">
          <div class="song-thumbnail"></div>
        </div>



        <div class="song-detail">
          <h1 class="song-title">红</h1>
          <span class="artist">罗言RollFish</span>
        </div>


       


        <div class="song-controls">
          <audio :src="songUrl" controls class="audio"></audio>
        </div>

      </div>
    </div>


    <div>
      <canvas></canvas>
    </div>
  </div>
</template>

<script>
import songUrl from '@/assets/mp3/rollfish.mp3';
export default {
  data() {
    return {
      songUrl,
      isLight: true
    }
  },
  mounted(){
    
    const audieEle=document.querySelector('audio');
    const cvs=document.querySelector('canvas');
    const ctx=cvs.getContext('2d');
    //????
    function initCvs(){
      cvs.width=500*devicePixelRatio;
      cvs.height=400/2*devicePixelRatio;
      
    }
    initCvs()
    let isInit=false;
    let dataArray,analyser;
    audieEle.addEventListener('play',()=>{
      if(isInit){
        return;
      }
      //初始化
      const audCtx=new AudioContext();
      const source=audCtx.createMediaElementSource(audieEle);
      analyser=audCtx.createAnalyser()
      analyser.fftSize=512;
      //创建数组
      dataArray=new Uint8Array(analyser.frequencyBinCount);
      source.connect(analyser);
      analyser.connect(audCtx.destination);

      isInit=true
    })

    function draw(){
      requestAnimationFrame(draw);
      const {width,height}=cvs;
      ctx.clearRect(0,0,width,height);
      //分析器分析数据到数组中
      if(!isInit) return
      analyser.getByteFrequencyData(dataArray);
      
      
      const len=dataArray.length/2.5;
      const barWidth=width/len/2;
      for(let i=0;i<len;i++){
        const data=dataArray[i];
        const barHeight=data/255*height;
        const x1=i*barWidth+width/2;
        const x2=width/2-(i+1)*barWidth;
        const y=height-barHeight;
        ctx.fillRect(x1,y,barWidth-2,barHeight);
        ctx.fillRect(x2,y,barWidth-2,barHeight);
      }
    }
    draw()
  }
}
</script>

<style scoped lang="less">
