<template>
    <form
      ref="form"
      class="data-form-container"
     @submit.prevent="handleSubmit"
    >
      <div class="form-item">
        <div class="input-area">
          <input
            type="text"
            maxlength="10"
            v-model="formData.nickname"
            placeholder="用户昵称"
          />
          <span class="tip">{{ formData.nickname.length }}/10</span>
        </div>
        <div class="error">{{ error.nickname }}</div>
      </div>
      <div class="form-item">
        <div class="text-area">
          <textarea
            maxlength="300"
            placeholder="输入内容"
            v-model="formData.content"
          ></textarea>
          <span class="tip">{{ formData.content.length }}/300</span>
        </div>
        <div class="error">{{ error.content }}</div>
      </div>
      <div class="form-item">
        <div class="button-area">
          <button :disabled="isSubmiting">
            {{ isSubmiting ? "提交中..." : "提交" }}
          </button>
        </div>
      </div>
    </form>
  </template>

<script>
import  showPopup  from '@/utils/showPopup.js';
export default {
    data(){
        return{
            formData:{
                nickname:'',
                content:''
            },
            error:{
                nickname:"",
                content:''
            },
            isSubmiting:false
        }

        
    },
    methods:{
      handleSubmit(){
      this.error.nickname=this.formData.nickname?'':'请填写昵称'
      this.error.content=this.formData.content?'':'请填写内容'
      if(this.error.nickname||this.error.content)
      {
        return
      }
        this.isSubmiting=true
        this.$emit('submit',this.formData,()=>{
          this.formData.nickname=''
          this.formData.content=''
          this.isSubmiting=false
          showPopup('提交成功！','success',2000,this.$refs.form)
        })
      }
    }
}
</script>

<style scoped lang="less">
@import "~@/styles/var.less";
.data-form-container {
  margin-bottom: 20px;
  overflow: hidden;
}
.form-item {
  margin-bottom: 8px;
}
.input-area {
  width: 50%;
  position: relative;
  input{
    font-family: "ZCOOL KuaiLe", "BlinkMacSystemFont", "Roboto", "Helvetica Neue",
      "微软雅黑", sans-serif;
    &::placeholder{
        font-family: "ZCOOL KuaiLe", "BlinkMacSystemFont", "Roboto", "Helvetica Neue",
      "微软雅黑", sans-serif;
  }
  }
  
}
.text-area {
  position: relative;
  textarea{
    font-family: "ZCOOL KuaiLe", "BlinkMacSystemFont", "Roboto", "Helvetica Neue",
      "微软雅黑", sans-serif;
    &::placeholder{
        font-family: "ZCOOL KuaiLe", "BlinkMacSystemFont", "Roboto", "Helvetica Neue",
        "微软雅黑", sans-serif;
    }
  }
}
.tip {
  position: absolute;
  right: 5px;
  bottom: 5px;
  color: #b4b8bc;
  font-size: 14px;
}
input,
textarea {
  display: block;
  width: 100%;
  box-sizing: border-box;
  border: 1px dashed @gray;
  outline: none;
  color: @words;
  font-size: 16px;
  border-radius: 4px;
  &:focus {
    border-color: @primary;
  }
}
input {
  padding: 0 15px;
  height: 40px;
}
textarea {
  resize: none;
  padding: 8px 15px;
  height: 120px;
}
.error {
  margin-top: 6px;
  color: @danger;
  font-size: 16px;
  height: 20px;
  line-height: 20px;
}
button {
    font-family: "ZCOOL KuaiLe", "BlinkMacSystemFont", "Roboto", "Helvetica Neue",
    "微软雅黑", sans-serif;
    font-size: 16px;
  position: relative;
  cursor: pointer;
  border: none;
  outline: none;
  width: 100px;
  height: 34px;
  color: #fff;
  border-radius: 4px;
  background: @primary;
  &:hover {
    background: darken(@primary, 10%);
  }
  &:disabled {
    background: lighten(@primary, 20%);
    cursor: not-allowed;
  }
}
</style>

