<template>
  <div class="blog-comment-container">
    <MessageBar class="message-bar-container" :comments="remoteData" v-if="remoteData" @submit="handleSubmit" :isListLoading="isListLoading"></MessageBar>
  </div>
  
</template>

<script>
import MessageBar from '@/components/MessageBar'
import fetchDataMixin from '@/mixins/fetchDataMixin'
import {getComments,postComment} from '@/api/blog.js'
import {debounce} from '@/utils'

export default {
    components:{
        MessageBar
    },
    mixins:[fetchDataMixin(null)],
    data(){
      return{
        page:1,
        isListLoading:false
      }
    },
    created(){

      
      this.$bus.$on('scroll',debounce(this.scrollHandler,50))
      
      
    },
    methods:{
        async fetchData(){
            const a=await getComments(this.$route.params.blogId,this.page)
            return a
            
            
        },
        async scrollHandler(scrollDom){
          
          const tmp=scrollDom.scrollHeight-scrollDom.scrollTop-scrollDom.clientHeight
          if(Math.abs(tmp)<100){
            this.fetchMore()
          }

        },
        async fetchMore(){
          if(this.remoteData.rows.length>=this.remoteData.total ||this.isListLoading) return
          
          
          this.isListLoading=true
          this.page++;
          const resp=await this.fetchData()
          this.remoteData.rows=this.remoteData.rows.concat(resp.rows)
          this.isListLoading=false

        },
        async handleSubmit(formData,callback){
          const ret=await postComment({
            blogId:this.$route.blogId,
            ...formData
          })
          
          
          this.remoteData.rows.unshift(ret)
          callback()
        }
    }
}
</script>

<style lang="less" scoped>
.message-bar-container{
  position: relative;
}
</style>

