<template>
    <div class="detail-container" v-loading="isLoading" >
        <Layout>
            <div class="main-container" ref="mainContainer" @scroll="handleScroll">                 
                <BlogContent :content="remoteData" v-if="remoteData"/>
                <BlogComment v-if="remoteData"></BlogComment>
                
            </div>

            <template #right>
                <div class="right-container">
                    <BlogToc :toc="remoteData.toc" v-if="remoteData"></BlogToc>
                </div>
            </template>
        </Layout>
    </div>
</template>

<script>
import Layout from '@/components/Layout/'
import BlogContent from './components/BlogContent.vue'
import BlogToc from './components/BlogToc.vue'
import BlogComment from './components/BlogComment.vue'
import fetchDataMixin from '@/mixins/fetchDataMixin.js'
import {getBlog} from '@/api/blog.js'

export default {
    components:{
        Layout,BlogContent,BlogToc,BlogComment
    },
    mixins:[fetchDataMixin(null)],
    methods:{
        async fetchData(){
            const a=await getBlog(this.$route.params.id)
            return a
        },
        handleScroll(){
           
            this.$bus.$emit('scroll',this.$refs.mainContainer)
            
        }
    },

   

}
</script>

<style scoped lang="less">
.detail-container{
    height: 100%;
    position: relative;
    .right-container{
        width: 300px;
        
    }
    .main-container{
    height: 100%;
    overflow-y:auto ;
    padding: 15px;
    scrollbar-color: #888 #f1f1f1; 
    scrollbar-width: thin;  
    scroll-behavior: smooth;
}
}
.blog-comment-container{
    margin-top: 50px;
}
</style>