import Home from '@/views/Home'
import Blog from '@/views/Blog'
import About from '@/views/About'
import Project from '@/views/Project'
import Message from '@/views/Message'
import Detail from '@/views/Blog/Detail'






export default [
    {name:"home", path: "/", component: Home },
    {name:"index", path: "/index.html", component: Home },
    {name:'blog', path: '/blog', component: Blog },
    {name:'about', path: '/about', component: About },
    {name:'project', path: '/gallery', component: Project },
    {name:'message', path: '/music', component: Message },
    {name:'cate',path: '/blog/cate/:cateId', component: Blog },
    {name:'detail',path: '/blog/:blogId', component: Detail },

]
