<template>
  
    <!-- <right-list :list="list"></right-list> -->
     <div class="blog-category-list" v-loading="isLoading">
      <h3>
        文章分类
      </h3>
      <right-list :list="cateList" v-if="cateList.length>1" @select="headleSelect"></right-list>
     </div>
  
</template>

<script>
import {getBlogType} from '@/api/blog.js' 
import RightList from './RightList.vue';
import fetchDataMixin from '@/mixins/fetchDataMixin'
export default {
  mixins:[fetchDataMixin([])],
  methods:{
    async fetchData(){
      const a=await getBlogType()
      return a
    },
    headleSelect(item){
      if(item.isSelected){
        return
      }
      
      
      this.$router.push({
        name:'cate',
        params:{
          cateId:item.id.toString()
        },
        query:this.routeInfo.query
      })
    
      
    }
  },
  components:{
    RightList
  },
  computed:{
    routeInfo(){
      
      const cateId=this.$route.params.cateId||-1
      const limit=+this.$route.query.limit||10
      const page=+this.$route.query.page||1
      return {
        params:{
          cateId
        },
        query:{
          limit,page
        }
      }
    },
    cateId:function(){
      return +this.$route.params.cateId||-1
    },
    cateList(){
      const sumCnt=this.remoteData.reduce((accumulator,current)=>{
        return accumulator+current.articleCount
      },0)
      const tmpArr=[{ id:-1,
        name:'全部',
        articleCount:sumCnt},...this.remoteData]

      const arr=tmpArr.map((item)=>{
        return {
          ...item,
          isSelected:item.id===this.cateId
        }
      })
      
      return arr
    }
  },

}
</script>

<style scoped lang="less">
.blog-category-list{
  padding: 5px;
  width: 200px;
  height: 100%;
  position: relative;
  h3{
    margin-bottom: 40px;
  }
}

</style>