<template>
  <div class="blog-list-container" v-loading="isLoading" ref="container">
    <ul>
      <li v-for="item in remoteData.rows" :key="item.id">
        <div class="thumb" v-if="item.thumb">
          <img :src="item.thumb" alt="">
        </div>
        <div class="main">
          <router-link :to="`/blog/${item.id}`">
            <h2 class="title">{{ item.title }}</h2>
          </router-link>
          <div class="header">
            <span>日期：{{ item.createDate}}</span>
            <span>浏览：{{ item.scanNumber }}</span>
            <span>评论：{{ item.commentNumber }}</span>
          </div>
          <div class="desc">
            <span>{{ item.description }}</span>
          </div>
        </div>
      </li>
    </ul>
    <div v-if="Object.keys(remoteData).length">
      <Pager :current="routeInfo.page" :limit="this.routeInfo.limit" :total="remoteData.total" :visibleNum="8" @changePage="handleChangePage"></Pager>
    </div>

    
  </div>
</template>

<script>
import {getBlogs} from '@/api/blog.js'
import Pager from "@/components/Pager";
import fetchDataMixin from '@/mixins/fetchDataMixin.js'

export default {
  components:{Pager},
  mixins:[fetchDataMixin({})],
  
  methods:{
    async fetchData(){
      return await getBlogs(this.routeInfo.page,this.routeInfo.limit,this.routeInfo.cateId)
    },
    handleChangePage(newPage){
      

      if(this.routeInfo.cateId===-1){
        this.$router.push({
        name:'blog',
        query:{
          page:newPage.toString(),
          limit:this.routeInfo.limit
        }
      })
      }else{
        this.$router.push({
        name:'cate',
        params:{
          cateId:this.routeInfo.cateId,
        },
        query:{
          page:newPage.toString(),
          limit:this.routeInfo.limit
        }
      })
      }
      
      
      

    }
  },
  computed:{
    routeInfo(){
      const cateId=this.$route.params.cateId||-1
      const limit=+this.$route.query.limit||10
      const page=+this.$route.query.page||1
      return {
        cateId,limit,page
      }
    }
    
  },
  watch:{
    async routeInfo(newVal,oldVal){
    
      
      this.$refs.container.scrollTop=0
      this.isLoading=true
      this.remoteData=await this.fetchData()
      this.isLoading=false
    }
  },  

  
}
</script>

<style scoped lang="less">
@import url("~@/styles/var.less");
.blog-list-container{
  position: relative;
  overflow-y: auto;
  scrollbar-color: #888 #f1f1f1; 
  scrollbar-width: thin;  
  scroll-behavior: smooth;
  width: 100%;
  height: 100%;


  ul{
    padding: 20px;
    li{
    margin-bottom: 35px;
    display: flex;
    .thumb{
      img{
      width: 250px;
      height: 200px;
      object-fit: cover;
      border-radius: 10%;
    }
    }
    .main{
      margin-left: 20px;
      .title{
        
      }
      .header{
        color: darken(@gray,15%);
        margin-top: 15px;
        span{
          margin-right: 10px;
        }
      }
      .desc{
        margin-top:50px ;
      }
    }
    
  }
  }
}
</style>