import myAxios from "./myAxios";

async function getBlogType() {
    return await myAxios.get("/api/blogtype");
  }

async function getBlogs(page=1,limit=10,categotyid=-1){
    return await myAxios.get('/api/blog',
        {
            params:{
                page,limit,categotyid
            }
        }
    )
}

export async function getBlog(id){
    return await myAxios.get(`/api/blog/${id}`)
}

export async function postComment(comment) {
    return await myAxios.post('/api/comment',comment)
}

export async function getComments(blogId,page=1,limit=5) {
    return await myAxios.get('/api/comment',{
        params:{
            blogId,page,limit
        }
    })
}

export {getBlogType,getBlogs}
