<template>
  <div class="blog-toc-container">
    <RightList :list="modifiedToc" @select="handleSelect"></RightList>
  </div>
</template>

<script>
import RightList from './RightList.vue';
import debounce from '@/utils/debounce';
export default {
    components:{
        RightList
    },
    props:{
        toc:{
            type:Array,
            required:true
        }
    },
    methods:{
        handleSelect(e){
            
            location.hash=e.anchor
        },
        
        setSelected(){
            this.whoSelected=''
            const range=400
            
            for (const item of this.titleDom) {
                const rect=item.getBoundingClientRect()
                if(rect.top>0&&rect.top<=range){
                    this.whoSelected=item.id
                    
                    break
                }
                if(rect.top>range){
                    
                    
                    break
                }
                if(rect.top<0){
                    
                    
                    
                    this.whoSelected=item.id
                }
                
                
            }
            
            
            
        }
    },
    data(){
        return{
            whoSelected:'',
            titleDom:[]
        }
    },
    
    mounted(){
        const tmpHash=location.hash
        location.hash=''
        
        location.hash=tmpHash
        this.setSelectedDebounce=debounce(this.setSelected,50)
        this.$bus.$on('scroll',this.setSelectedDebounce)
        for (const name of this.titleName) {
            
            this.titleDom.push(document.querySelector(`#${name}`))
        }
        
    },
    
    destroyed(){
        this.$bus.$off('scroll',this.setSelectedDebounce)
    },
    computed:{
        modifiedToc(){
            const modifyToc=(toc=[])=>{
                return toc.map((node)=>{
                    
                    let newNode={
                        ...node,
                        isSelected:this.whoSelected===node.anchor,
                    }
                    if(node.children){
                        newNode.children=modifyToc(node.children)
                    }
                    return newNode
                })
            }


            return modifyToc(this.toc)
        },
        titleName(){
            let arr=[];
            function pre(forest=[]){
                if(!forest||forest.length<1) return
                for (const node of forest) {
                    arr.push(node.anchor)
                    if(node.children){
                        pre(node.children)
                    }
                }
            }
            pre(this.toc)
            return arr
        }
    }
    
}
</script>

