<template>
  <ul class="right-list-container" v-if="list.length>0">
      <li v-for="(item,index) in list" :key="index">
        <span  :class="{active:item.isSelected}" @click="handleClick(item)">{{ item.name }}</span>
        <span v-if="item.articleCount" class="note"> &lt;{{ item.articleCount }}&gt;</span>
        <RightList :list="item.children" v-if="item.children" v-on="$listeners">
        </RightList>
      </li>
      
  </ul> 
  
</template>

<script>
export default {
    name:'RightList',
    props:{
        list:{
            type:Array,
            default:()=>[]
        }
    },
    methods:{
      handleClick(item){
        this.$emit('select',item)
        
      }
    },
    
}


</script>

<style scoped lang="less">
.right-list-container {
  .right-list-container {
    margin-left: 100px;
  }
  >li {
    min-height: 30px;
    line-height: 30px;
    margin-bottom: 10px;
    span{
      cursor: pointer;
    }
    .active {
      color: green;
      font-weight: bold;
    }
    .note{
      font-size: 15px;
      margin-left: 5px;
    }
  }
}
</style>